@import url('https://fonts.cdnfonts.com/css/stack-ssi?styles=138215');

.wForm form {
  display: grid;
  justify-content: center;
}

.htmlSection {
  width: 100%;
}

.htmlSection h2 {
  width: 100%;
  line-height: 1.25;
  color: #000;
  font-weight: 700;
  font-family: 'Stag Sans', sans-serif;
  font-size: 50px;
}

.htmlContent,
.htmlSection,
.htmlSection p, 
.htmlSection span,
.htmlSection .htmlContent,
.htmlSection .htmlContent span {
  font-size: 25px !important;
  font-family: 'Arial', sans-serif;
  color: #000 !important;
}

.wfContent .oneFieldWrapper .label,
form .oneField .label {
  font-size: 20px;
  font-family: 'Arial', sans-serif;
  font-weight: 700;
  line-height: 1.25;
  color: #000 !important;
}

.inputWrapper  input,
.inputWrapper  input[type='text'].validate-email {
  width: 100% !important;
  height: 76px !important;
}

.wfContent .inputWrapper {
  height: 100% !important;
  width: 100% !important;
}

.inputWrapper,
.oneFieldWrapper,
.inputContainer {
  width: 100% !important;
}

.wFormContainer .actions .primaryAction,
.wForm form .actions .primaryAction {
  border: 1px solid #000 !important;
}
